import React, { Component } from 'react';
import {render} from 'react-dom';
import NotFound from '../NotFound/NotFound.js';
import style from './style.css';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import vars from '../../utils/variables.js';
const{api} = vars;
import {Router} from 'react-router';
const transitionTo = Router.transitionTo;
import { hashHistory } from 'react-router';
import Auth from '../../modules/Auth.js';
var RouteHandler = Router.RouteHandler;
import cookie from 'react-cookie';

export default class LoginPage extends Component{
	constructor(props,context) {
		super(props,context);
		this.state ={
			password: '',
			errMsg: ''
		};
		this.showErrorWrongPassword = this.showErrorWrongPassword.bind(this);
		this.alertError = this.alertError.bind(this);
	}

	handleLogin(e){
		e.preventDefault();
		const password = encodeURIComponent(this.state.password);
		const formData = `password=${password}`;
		if (this.state.password.trim().length === 0) {
			this.setState({errMsg: 'Please enter a password'})
		}
		else{
		fetch(`${api}react/authenticate`,{
			method: 'POST',
			headers:{
			'Content-Type': 'application/json'
			},
			body: JSON.stringify({'password':this.state.password})
		})
		.then(response => response.json())
		.then(data => {
			if(!data.success) this.setState({errMsg: data.message}) 
			else{ 
				// add httpOnly:true
				cookie.save('login_data',data.token,{path : '/'}),
				window.location='/';
				}
		})
		.catch(error => console.log('error fetch', error))
  }
			// data.success ? this.redirectToNextPage() : this.showErrorWrongPassword()})
}
	redirectToNextPage(){
		console.log( 'logged in');
		hashHistory.push('/');
		// save cookies, show green ok etc
	}

	showErrorWrongPassword(){
		this.setState({errMsg: 'Wrong password'})
		// show wrong password prompt
	}

	alertError(){
		alert('server internal error.')
	}

	handleInputChange(e){
		this.setState({password: e.target.value})
	}

	
	render() {
		return (
			<MuiThemeProvider>
			<div className={style.background}>
			<div className={style.container}>
					<div className={style.title}>
						Admin Panel Login
					</div>
					<form action ="/" onSubmit={this.handleLogin}>
				<TextField floatingLabelText="Enter admin password" type="password" onChange={this.handleInputChange.bind(this)} errorText={this.state.errMsg} /> <br/>
				<RaisedButton type="submit" label="Login" primary={true} onClick={this.handleLogin.bind(this)} className={style.btnLogin}/>
				</form>
			</div>
			</div>
			</MuiThemeProvider>
		);
	}

	componentDidMount() {
		// fetch('https://api.github.com/users',{
		// 	method: 'GET',
			
		// })
		// .then(response => response.json())
		// .then(data => {
		// console.log('git data', data)
		// })
		// .catch(error => console.log('error git', error))
	}
}



