'use strict'
import cookie from 'react-cookie';
import variables from '../utils/variables.js';
const{api} = variables;
import {browserHistory } from 'react-router'

export default class Auth {

  /**
   * Authenticate a user. Save a token string in Local Storage
   *
   * @param {string} token
   */
  static authenticateUser(token) {
    cookie.save('login_data',data.token,{path : '/',httpOnly:true})  }

  /**
   * Check if a user is authenticated - check if a token is saved in Local Storage
   *
   * @returns {boolean}
   */
  static isAuthenticated() {

    // console.log('cookie: ', cookie.load('login_data'))
    if (cookie.load('login_data') === undefined) {
      return false;
    }
    return true;
  }

  static getCookie(){
    return cookie.load('login_data');
  }
  /**
   * Deauthenticate a user. Remove a token from Local Storage.
   *
   */
  static deauthenticateUser() {
    cookie.remove('login_data');
  }

  /**
   * Get a token value.
   *
   * @returns {string}
   */



  // check if user is authorized
  static requireAuth(){
    if (cookie.load('login_data') == null && document.location.pathname !== '/login'){
      browserHistory.push('/login');
      console.log( 'cookie null');
    }
    else{
     fetch(`${api}react/home`,{
      headers:{
        'Authorization': cookie.load('login_data')
      }
    }).then(response => response.json())
    .then(data => {
      if (!data.success) {
        cookie.remove('login_data');
        browserHistory.push('/login');

      }})
    .catch(error => {
      browserHistory.push('/login');
      
    })

    }
  }





}



