import React, { Component } from 'react';
import {render} from 'react-dom';
import Card from '../Card/Card.js';
import variables from '../../utils/variables.js';
const{api} = variables;
import style from './style.css';
import {Row} from 'react-bootstrap';
import Modal from '../Modal/Modal.js';


// holder of all the cards
export default class CardsCollector extends Component{
  constructor(props) {
    super(props);
    this.state = {
      images: [],
      isModalVisible: false,
      // for showing modals
      currentUser: null,
      userGroupsNum: null,
      userImagesNum:null
    }

  }
  delete(item){
    let newState = this.state.images;
    if (newState.indexOf(item) >-1) {
      newState.splice(newState.indexOf(item),1);
      fetch(`${api}images/id/${item._id}`,{
        method: "DELETE"
      }).then(response => {
        if(response.ok)  {
        this.setState({images:newState})
        console.log(` DELETE item OK. id : ${item._id}`)}
        else console.log(` DELETE item ERROR. id :${item._id}`)
      }
      , (error => alert(error.message)))

    }
  }
  componentDidMount() {
    fetch(`${api}images/all`)
    .then(response => response.json())
    .then(images => {
      this.setState({images: images.message})})
     .catch((error) => {
        console.error(error);
    });
  }
// toggle modal after click
  toggleModal(){
    const isVisible = this.state.isModalVisible;
    this.setState({isModalVisible: !isVisible});
  }
//  show modal with user details
  show(user){
    this.setState({isModalVisible: true, currentUser: user});
    this.handleUserImagesNum(user._id);
    this.handleUserGroupsNum(user._id);
    // assign props to modal, name , class visible
  }

// ban user
  banUser(user){
    fetch(`${api}users/id/${user._id}/toggleban`,{
      method: "GET",
        }).then(response => {
      if(response.ok && !response.json().error){
      alert(user.name + ' is banned')
      console.log(response)
    }
      else
      console.log("err")
    }).catch((error) => {
      console.error(error);
    });
    
  }

  handleUserImagesNum(id){
    function getImagesPromise(){
      return fetch(`${api}images/user_id/${id}`)
    .then(response => response.json())
    .then(responseText =>{
      return responseText.message.length;
    }).catch(error => console.log(error))
    }
    getImagesPromise().then(response =>{
      this.setState({userImagesNum:response})
    });
  }
  
  
  handleUserGroupsNum(id){
    function getGroupsPromise() {
      return fetch(`${api}groups/user_id/${id}`)
      .then(response => response.json())
    .then(responseText =>{
      console.dir('message: '+ responseText.message);
      return responseText.message.length;
    }).catch(error => console.log(error))
    }
    getGroupsPromise().then(response =>{
      this.setState({userGroupsNum:response})
    });
  }


  getPostsNum(id){
    // fetch(`${api}images/user_id/$
  }
  

 render(){
    return(
    <div>
        {this.state.images.filter((image) =>{
          return !image.user_id.isBanned
        }).map((image,index) => {
           return <Card key={index} title={image.title} image={image.image_string} deleteHandler={this.delete.bind(this,image)} showModal={this.show.bind(this,image.user_id)} authorName={image.user_id.name}> {image.title} </Card> 
           
          // else if( index % 4 === 3 ){return <Card key={index} image={image.image_string} deleteHandler={this.delete.bind(this,image)} showModal={this.show.bind(this,image.user_id)}> {image.title} </Card> </Row>;
      })
      }

      {this.state.isModalVisible ?
       <Modal tabIndex="0" profile={this.state.currentUser.profile_image}
        user={this.state.currentUser}
        name={this.state.currentUser.name}
        isBanned={this.state.currentUser.isBanned}
        closeModal={this.toggleModal.bind(this)} 
        banUser={this.banUser.bind(this,this.state.currentUser)}
        groupsNum={this.state.userGroupsNum} postsNum={this.state.userImagesNum}/> : null}
    
    </div>
    );
  }
}



